import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { Ec2ListComponent } from './components/ec2-list/ec2-list.component';
import { TestCompComponent } from './components/test-comp/test-comp.component';
import { Dashboard2Component } from './components/dashboard2/dashboard2.component';

const routes: Routes = [
  {
    path:'', 
    component:HomeComponent,
    children:[
      {path:'', redirectTo:'dashboard',pathMatch:'full'},
      // {path:'dashboard', component:DashboardComponent, pathMatch: 'full'},
      {path:'dashboard', component:Dashboard2Component, pathMatch: 'full'},
      {path:'ec2list', component:Ec2ListComponent, pathMatch: 'full'},
      {path:'test', component:TestCompComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRoutingModule { }
