import { Component, OnInit, Output, Input, EventEmitter, Inject, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { MatAutocompleteTrigger, MatAutocomplete } from '@angular/material/autocomplete';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';

import { SearchListDialogComponent } from '../../shared/components/search-list-dialog/search-list-dialog.component';

@Component({
  selector: 'app-top-nav',
  templateUrl: './top-nav.component.html',
  styleUrls: ['./top-nav.component.scss']
})
export class TopNavComponent implements OnInit {
  @Output() toggleSideNav = new EventEmitter<any>();
  @ViewChild(MatAutocompleteTrigger) autocomplete: MatAutocompleteTrigger;
  // @Input('matAutocomplete') autocomplete: MatAutocomplete

  searchControl = new FormControl();
  searchOptions: string[] = ['Cloud Restore', 'Penny Pincher', 'Governor', 'Alarms', 'Tag Checks', 'QAR', 'Encryption', 'Jobs', 'Settings', 'Shutdowns', 'Unused Resources', 'Right Sizing', 'Reports', 'Accounts'];
  filteredOptions: Observable<string[]>;

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.searchOptions.filter(option => option.toLowerCase().includes(filterValue));
  }
  toggleSideNavMenu() {
    this.toggleSideNav.emit();
  }

  onSearch() {
    // this.autocomplete.showPanel = false;
    console.log(this.autocomplete)
    if (!this.searchControl.value) {
      this.autocomplete.closePanel();
      const dialogRef = this.dialog.open(SearchListDialogComponent, {
        width: '400px',
        data: { searchOptions: this.searchOptions }
      });

      dialogRef.afterClosed().subscribe(result => {
        if(result){
          this.searchControl.setValue(result);
        }
      });
    }
  }
  constructor(public dialog: MatDialog) { }

  ngOnInit() {
    this.filteredOptions = this.searchControl.valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
  }

}
