import { Component, OnInit, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';
import { DashboardService } from '../../../../shared/services/dashboard.service';

@Component({
  selector: 'app-cost',
  templateUrl: './cost.component.html',
  styleUrls: ['./cost.component.scss']
})
export class CostComponent implements OnInit, AfterViewInit {
  @ViewChild('canvas') canvas: ElementRef;

  chart = {};

  constructor() { }

  ngOnInit() {
  }
  ngAfterViewInit() {
    this.chart = new Chart(this.canvas.nativeElement.getContext('2d'), {
      type: 'line',
      data: {
        labels: ["January", "February", "March", "April", "May", "June", "July"],
        datasets: [{
          label: "Actual cost",
          borderWidth:1,
          backgroundColor: 'rgba(235, 160, 71, 0.7)',
          borderColor: 'rgba(235, 160, 71, 0.7)',
          // backgroundColor: 'rgba(66, 66, 66, 0.7)',
          // borderColor: 'rgba(66, 66, 66, 0.7)',
          fill: true,
          data: [40, 24, 15, 33, 24, 55, 64],
        },
        {
          label: "Attained cost",
          borderWidth:1,
          backgroundColor: 'rgba(217, 53, 74, 0.7)',
          borderColor: 'rgba(217, 53, 74, 0.7)',
          // backgroundColor: 'rgba(66, 66, 66, 0.7)',
          // borderColor: 'rgba(66, 66, 66, 0.7)',
          fill: true,
          data: [35, 14, 25, 16, 36, 42, 54],
        }, {
          label: "Project cost",
          borderWidth:1,
          backgroundColor: 'rgba(107, 162, 123, 0.7)',
          borderColor: 'rgba(107, 162, 123, 0.7)',
          //           backgroundColor: 'rgba(66, 66, 66, 0.7)',
          // borderColor: 'rgba(66, 66, 66, 0.7)',
          fill: true,
          data: [20, 17, 55, 32, 18, 29, 44],
        }]
      },
      options: {
        // maintainAspectRatio:true,

        responsive: true,
        // maintainAspectRatio: false,
        legend: {
          display: true,
        },
        tooltips: {
          mode: 'index',
          intersect: false,
        },
        hover: {
          mode: 'nearest',
          intersect: true
        },
        scales: {
          xAxes: [{
            // stacked:true,
            display: true,
            gridLines: {
              display: false
            }
          }],
          yAxes: [{
            stacked:true,
            display: true,
            gridLines: {
              display: false
            }
          }],
        }
      }
    });
  }
}