import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource, MatTable } from '@angular/material';
import { PageEvent } from '@angular/material/paginator';
import {merge, Observable, of as observableOf} from 'rxjs';
import {catchError, map, startWith, switchMap} from 'rxjs/operators';

import { CommonService } from '../../../shared/services/common.service';


@Component({
  selector: 'app-ec2-list',
  templateUrl: './ec2-list.component.html',
  styleUrls: ['./ec2-list.component.scss']
})
export class Ec2ListComponent implements OnInit {
  displayedColumns: string[] = [];
  dataSource: MatTableDataSource<x>;
  // dataSource: MatTableDataSource<any> = new MatTableDataSource();
  // dataSource: testTable[]  = [];
  resultsLength = 0;
  currentOffset = 0;
  pageIndex = 0;
  pageSize = 3;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatTable) table: MatTable<any>;

  constructor(public commonService: CommonService) {
  }

  ngOnInit() {
    // this.fetchData(this.currentOffset);
    this.commonService.getData(this.currentOffset)
    .subscribe((data: any) => {
      console.log(this.dataSource);
      // this.dataSource.data = data.data;
      this.dataSource = new MatTableDataSource(data.data);
      this.displayedColumns = Object.keys(data.data[0]);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.resultsLength = data.total;
    });
  }
  fetchData(pageNo){
    this.commonService.getData(pageNo)
    .subscribe((data: any) => {
      console.log(data);
      // this.dataSource._pageData = data.data;
      // this.changeDetectorRefs.detectChanges();
      // this.pageSize = 3;
      // this.table.renderRows();
      this.dataSource = new MatTableDataSource(data.data);
      // this.displayedColumns = Object.keys(data.data[0]);
      // this.dataSource.paginator = this.paginator;
      // this.dataSource.sort = this.sort;
      // this.resultsLength = data.total;
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  onPaginateChange(event: PageEvent) {
    let offset = event.pageSize * event.pageIndex
    // console.log('event.pageSize: ', event.pageSize)
    // console.log('event.pageIndex: ', event.pageIndex)
    console.log(offset);
    console.log(this.currentOffset * 3);
    if(offset === (this.currentOffset+1) * 3 ){
      console.log("next called")
      // this.pageIndex = this.pageIndex + 1;
      this.currentOffset = this.currentOffset+1;
      this.fetchData(this.currentOffset)
    }
    else if(offset === (this.currentOffset-1) * 3 ){
      console.log("prev called ", this.currentOffset -1)
      // this.pageIndex = this.pageIndex + 1;
      this.currentOffset = this.currentOffset -1;
      this.fetchData(this.currentOffset)
    }
  }

}

export interface x {
  id:number,
  first_name:string,
  last_name:string,
  avatar:'string'
}