import { Component, OnInit, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';

@Component({
  selector: 'app-compliance',
  templateUrl: './compliance.component.html',
  styleUrls: ['./compliance.component.scss']
})
export class ComplianceComponent implements OnInit {
  @ViewChild('canvas') canvas: ElementRef;

  chart = {};

  constructor() { }

  ngOnInit() {
    console.log(this.chart);
  }
  ngAfterViewInit() {
    this.chart = new Chart(this.canvas.nativeElement.getContext('2d'), {
      type: 'doughnut',
			data: {
				datasets: [{
					data: [
            120,90,160
					],
					backgroundColor: [
            'rgba(235, 160, 71, 1)',
            'rgba(217, 53, 74, 1',
            'rgba(107, 162, 123, 1)',
          ],
          borderColor: [
            'rgba(235, 160, 71, 1)',
            'rgba(217, 53, 74, 1',
            'rgba(107, 162, 123, 1)',
					],
					label: 'Dataset 1'
				}],
				labels: [
					'Untagged',
					'Large',
					'Normal',
				]
			},
			options: {
				responsive: true,
				legend: {
					position: 'top',
				},
				animation: {
					animateScale: true,
					animateRotate: true
        },
        scales: {
          xAxes: [{
            display: false
          }],
          yAxes: [{
            display: false
          }],
        }
			}
    });

  }
}
