import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {MatPaginator, MatSort, MatTableDataSource} from "@angular/material";
import {debounceTime, distinctUntilChanged, startWith, tap, delay} from 'rxjs/operators';
import {merge} from "rxjs/";
import {fromEvent} from 'rxjs/';

import { CommonService } from '../../../shared/services/common.service';
import { TableDataSource } from '../../../shared/services/table.datasource';

@Component({
  selector: 'app-test-comp',
  templateUrl: './test-comp.component.html',
  styleUrls: ['./test-comp.component.scss']
})
export class TestCompComponent implements OnInit {
  course:any;

  dataSource: TableDataSource;

  displayedColumns= ["id", "first_name", "last_name"];

  @ViewChild(MatPaginator) paginator: MatPaginator;

  @ViewChild(MatSort) sort: MatSort;

  @ViewChild('input') input: ElementRef;

  constructor(private route: ActivatedRoute,
              private commonService: CommonService) {

  }

  ngOnInit() {

      this.course = 1;

      this.dataSource = new TableDataSource(this.commonService);

      this.dataSource.loadLessons(this.course);
  }

  ngAfterViewInit() {

      this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);

      fromEvent(this.input.nativeElement,'keyup')
          .pipe(
              debounceTime(150),
              distinctUntilChanged(),
              tap(() => {
                  this.paginator.pageIndex = 0;

                  this.loadLessonsPage();
              })
          )
          .subscribe();

      merge(this.sort.sortChange, this.paginator.page)
      .pipe(
          tap(() => this.loadLessonsPage())
      )
      .subscribe();

  }

  loadLessonsPage() {
      this.dataSource.loadLessons(
          this.course.id)
  }

}

